/**
 * Sample plugin.
 * This plugin is used to demonstrate implementation of lifecycle hooks and
 * implementation of plugin functions.
 *
 * The following hook(s) are implemented:
 * 1. onBeforeContainerLoad - used here to print to the console.
 * 2. onBeforeAppLoad - marks app-3 as a "soon to be decommissioned app".
 *
 * The following function(s) are implemented:
 * 1. sampleFunction - dispatches a notification.
 */

/**
 * Lifecycle hook executed automatically on load of the
 * Container.
 * Implement this function when you want code in
 * the plugin to execute on load of the Container.
 *
 * This is a non blocking call.
 *
 * @function onBeforeContainerLoad
 * @param {Object} data - { store }
 * @public
 */
const onBeforeContainerLoad = () => (resolve) => {
   console.log('onBeforeContainerLoad() executed from plugin');
  resolve();
};

let disableApp3 = false;

/**
 * Lifecycle hook executed automatically on load of an APP.
 * Implement this function when you want code in
 * the plugin to execute before load of an APP.
 *
 * @param {Object} data - data for the plugin to execute.
 */
const onBeforeAppLoad = data => (resolve, reject) => {
  console.log('onBeforeAppLoad() executed from plugin, received data: %o', data);
  if (data.appName === 'app-3') {
    if (disableApp3) {
      reject('app-3 is disabled');
    } else {
      resolve();
    }
  } else {
    resolve();
  }
};

/**
 * Sample function can be called anytime.
 *
 * @function sampleFuncOne
 * @public
 */
const sampleFunction = (data) => {
  disableApp3 = data.disableApp3;
};

export default {
  onBeforeContainerLoad,
  onBeforeAppLoad,
  sampleFunction,
};
